import Link from "next/link"
import { Button } from "@/components/ui/button"

export function Navbar() {
    return (
        <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
            <div className="container flex h-16 items-center justify-between">
                <Link href="/" className="flex items-center space-x-2">
                    <span className="text-2xl font-bold bg-gradient-to-r from-green-600 to-green-400 bg-clip-text text-transparent">
                        GFG
                    </span>
                    <span className="text-sm font-medium text-muted-foreground hidden sm:inline-block">
                        Student Chapter
                    </span>
                </Link>

                <nav className="flex items-center gap-6">
                    <Link href="#events" className="text-sm font-medium hover:text-primary transition">
                        Events
                    </Link>
                    <Link href="#team" className="text-sm font-medium hover:text-primary transition">
                        Team
                    </Link>
                    <Link href="#about" className="text-sm font-medium hover:text-primary transition">
                        About
                    </Link>
                </nav>

                <div className="flex items-center gap-4">
                    <Link href="/member/dashboard">
                        <Button variant="ghost">Member Login</Button>
                    </Link>
                    <Link href="/admin/dashboard">
                        <Button>Lead Login</Button>
                    </Link>
                </div>
            </div>
        </header>
    )
}
